import { MetaFunction, LoaderFunction, useLoaderData, Link } from 'remix'
import { getPosts } from '~/post'

export const loader: LoaderFunction = () => {
  return getPosts()
}

export const meta: MetaFunction = () => {
  return {
    title: 'Homepage',
    description: 'Welcome to my homepage!',
  }
}

export default function Index() {
  const posts = useLoaderData<any[]>()

  return (
    <div>
      <h1>Recent blog posts</h1>
      <ul>
        {posts.map((post) => (
          <li key={post}>
            <h2>{post}</h2>
            <p>
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Veritatis
              officiis quo accusamus unde. Fugit, aspernatur? Iste repellendus
              laborum nesciunt voluptatibus!
            </p>
            <Link to={`posts/${post}`}>Read more →</Link>
          </li>
        ))}
      </ul>
    </div>
  )
}
